@import "mixins";

/*------------------------------------*\
Loading
\------------------------------------*/
.Loading {
  &__bar {
    height: 1px;
    position: fixed;
    top: 50%;
    left: 0;
    background-color: #fff;
    z-index: 1;
  }
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  &__up {
    width: 100%;
    height: 50%;
    position: absolute;
    top: 0;
    left: 0;
    background: #000;
  }
  &__down {
    width: 100%;
    height: 50%;
    position: absolute;
    bottom: 0;
    left: 0;
    background: #000;
  }
}

/*------------------------------------*\
Typing
\------------------------------------*/
#js-target {
  display: none;
}

/*------------------------------------*\
Skill
\------------------------------------*/
.Skill {
  margin-bottom: 8rem;
  &__wrapper {
    display: flex;
    margin-bottom: 4.8rem;
    align-items: flex-start;
    @include sp {
      flex-wrap: wrap;
      text-align: center;
    }
  }
  &__nubmer {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  &__detail {
    margin-left: 2rem;
    border-radius: 1rem;
    padding: 2rem;
    @include sp {
      margin-left: 0;
      padding: 2rem 0;
    }
  }
  &__head {
    margin-bottom: 2rem;
    font-weight: bold;
    font-size: 3rem;
    color: #86b7f2;
  }
  &__body {

  }
  &__img {
    @include sp {
      text-align: center;
      margin: 0 auto;
      transform: translateX(-10%);
    }
  }
}

.Skill__number {
  position: absolute;
  top: 12%;
  left: 45%;
  font-size: 8.8rem;
  color: #86b7f2;
  font-family: Pr6n-r;
}

.skill-img {
  background-image: url(../images/no-number.png);
  background-size: cover;
  height: 16rem;
  width: 19rem;
  position: relative;

}

/*------------------------------------*\
Profile
\------------------------------------*/
.Profile {
  &__container {
    padding: 10rem 5rem;
    transform: skew(0deg, -4deg);
    background: #dde4e8;
    color: #333;
    margin-bottom: 12rem;
    @include sp {
      padding: 10rem 0;
    }
  }
  &__wrapper {
    display: flex;
    align-items: flex-start;
    @include sp {
      flex-wrap: wrap;
    }
  }
  &__body {
    padding: 4rem;
    @include sp {
      padding: 0;
    }
  }
  &__img {
    filter: drop-shadow(-11px -2px 26px rgba(0, 0, 0, 0.14));
    text-align: center;
    @include sp {
      width: 100%;
      margin-bottom: 4rem;
    }
  }
}

/*------------------------------------*\
Footer
\------------------------------------*/

footer {
  color: #000;
  overflow: hidden;
  padding: 1rem 0;
  text-align: center;
}

footer small {
  font-size: 1.4rem;
}

footer small {
  font-family: 'Muli', sans-serif;
  letter-spacing: 0;
}

.footer_ul {
  display: -webkit-flex;
  display: flex;
  justify-content: center;
}

.footer_ul li {
  padding: 0 4rem;
  color: #000000;
  @include sp {
    padding: 0 1rem;
  }
}

